import React from "react";
import PaymentMethod from "./PaymentMethod";
import { useState } from "react";
import { CreditCard } from "lucide-react";
function Payment(props) {
  const [selected, setSelect] = useState(1);
  function hanldeSelect(id){
    if (id===1) props.onSelect({paymentMethod:"COD"});
    else props.onSelect({paymentMethod:"ONLINE"});
    setSelect(id);
  }
  return (
    <div className="order-info">
      <span style={{ fontWeight: "500" }}>
        <CreditCard
          color="#ff6a00"
          style={{ marginRight: "5px", marginBottom: "5px" }}
        />
        Phng thc thanh ton
      </span>
      <span style={{ opacity: "0.5", marginLeft: "5px", marginBottom:"10px" }}>
        Chn phng thc thanh ton ph hp
      </span>
      <PaymentMethod
        id={1}
        selected={selected}
        title="Thanh ton khi nhn hng (COD)"
        description="Thanh ton bng tin mt khi nhn hng"
        onSelect={hanldeSelect}
      />
      <PaymentMethod
        id={2}
        selected={selected}
        title="Chuyn khong ngn hng"
        description="Chuyn khong qua ti khon ngn hng"
        onSelect={hanldeSelect}
      />
      <PaymentMethod
        id={3}
        selected={selected}
        title="V MoMo"
        description="Thanh ton qua v MoMo"
        onSelect={hanldeSelect}
      />
      <PaymentMethod
        id={4}
        selected={selected}
        title="Th tn dng/ Ghi n"
        description="Visa, Mastercard, JCB"
        onSelect={hanldeSelect}
      />
    </div>
  );
}
export default Payment;
