import React from "react";
import ActionBar from "./ActionBar";
import MyProduct from "./MyProduct";
import MyOrder from "./MyOrder";
import AboutMe from "./AboutMe";
import Setting from "./Setting";
import { useState } from "react";
function ProfileAction(props) {
  const [current, setCurrent] = useState("Sn phm ca ti");
  function handleCurrent(chosen) {
    setCurrent(chosen);
  }
 
  return (
    <div className="container-action">
      <ActionBar onCurrent={handleCurrent} />
      {current === "Sn phm ca ti" && <MyProduct />}
      {current === "n hng ca ti" && <MyOrder />}
      {current === "Gii thiu" && <AboutMe user={props.user} />}
      {current === "Ci t" && <Setting user={props.user} />}
    </div>
  );
}
export default ProfileAction;
