import React from "react";
import TypeAction from "./TypeAction";
import { useState } from "react";
function FilterNotification(props) {
    const [chosen,setChosen]=useState("Tt c");
    function handleChoose(chosen){
        setChosen(chosen);
        props.onChange(chosen);
    }
  return (
    <div className="filter-bar row">
      <TypeAction type="Tt c" chosen={chosen} onChosen={handleChoose}/>
      <TypeAction type="Ngi dng" chosen={chosen} onChosen={handleChoose}/>
      <TypeAction type="n hng ca ti" chosen={chosen} onChosen={handleChoose}/>
      <TypeAction type="H thng" chosen={chosen} onChosen={handleChoose}/>
    </div>
  );
}
export default FilterNotification;
