import React, { use, useState } from "react";
import { MapPin } from "lucide-react";
function OrderInfo(props) {
  const message=props.message;
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [note, setNote] = useState("");
  function handleChange(target, e) {
    if (target === "phone") {
      setPhone(e.target.value);
      props.onEnter({
        phone: e.target.value,
      });
    } else if (target === "address") {
      setAddress(e.target.value);
      props.onEnter({
        address: e.target.value,
      });
    } else {
      setNote(e.target.value);
      props.onEnter({
        note: e.target.value,
      });
    }
  }
  return (
    <div className="order-info">
      <span style={{ fontWeight: "500" }}>
        <MapPin color="#ff6a00" style={{ marginRight: "5px" }} />
        Thng tin giao hng
      </span>
      <span style={{ opacity: "0.5", marginLeft: "5px" }}>
        Nhp thng tin giao hng ca bn
      </span>
      <form>
        <div className="row mt-3">
          <div className="col-6">
            <label htmlFor="InputPhone" className="form-label">
              S in thoi*
            </label>
            <input
              type="text"
              id="InputPhone"
              className="form-control"
              placeholder="Nhp s in thoi ca bn"
              style={{ borderRadius: "0.5rem" }}
              onChange={(e) => handleChange("phone", e)}
            />
          </div>
           <span style={{ color: "red" }}>{message.phone}</span>
          <div className="col-12 mt-3">
            <label htmlFor="InputAddress" className="form-label">
              a ch*
            </label>
            <input
              type="text"
              id="InputAddress"
              className="form-control"
              placeholder="S nh, tn ng, phng/x, qun/huyn, tnh/thnh ph"
              style={{ borderRadius: "0.5rem" }}
              onChange={(e) => handleChange("address", e)}
            />
          </div>
           <span style={{ color: "red" }}>{message.address}</span>
          <div className="col-12 mt-3 mb-3">
            <label htmlFor="InputNote" className="form-label">
              Ghi ch
            </label>
            <input
              type="text"
              id="InputNote"
              className="form-control"
              placeholder="Ghi ch thm v a ch (ty chn)"
              style={{ borderRadius: "0.5rem" }}
              onChange={(e) => handleChange("note", e)}
            />
          </div>
           <span style={{ color: "red" }}>{message.note}</span>
           <span style={{ color: "red" }}>{message.order}</span>
        </div>
      </form>
    </div>
  );
}
export default OrderInfo;
