import React from "react";
import { useState } from "react";
import TypeAction from "../../NotificationPage/ActionComponent/TypeAction";
function ActionBar(props) {
  const [chosen, setChosen] = useState("Sn phm ca ti");
  function handleChoose(chosen) {
    setChosen(chosen);
    props.onCurrent(chosen);
  }
  return (
    <div className="filter-bar row">
      <TypeAction
        type="Sn phm ca ti"
        chosen={chosen}
        onChosen={handleChoose}
      />
      <TypeAction
        type="n hng ca ti"
        chosen={chosen}
        onChosen={handleChoose}
      />
      <TypeAction type="Gii thiu" chosen={chosen} onChosen={handleChoose} />
      <TypeAction type="Ci t" chosen={chosen} onChosen={handleChoose} />
    </div>
  );
}
export default ActionBar;
