import React from "react";
import { useState } from "react";
function HeaderFeedback(props) {
  const [newest,setNewest] =useState(true);
  function hanldeNewest(){
    setNewest(!newest);
    props.onSort(newest);
  }
  return (
    <div className="header-feedback">
      <span style={{ color: "black", fontWeight: "500", fontSize: "17px",marginRight:"auto" }}>
        NH GI SN PHM
      </span>
      <div className={`filter-feedback ${!newest && "selected-feedback"}`} onClick={hanldeNewest}>Tt c</div>
       <div className={`filter-feedback ${newest && "selected-feedback"}`} onClick={hanldeNewest}>Mi nht</div>
    </div>
  );
}
export default HeaderFeedback;
