import React, { useEffect, useState } from "react";
import { formatTime } from "../../../utils/utils";
import { getMyProduct } from "../../../services/productService";
import { getFollowersCount, getFollowingCount } from "../../../services/followService";
function AboutMe(props) {
  const user = props.user;
  const [listProduct, setList] = useState([]);
  const [follower,setFollower]=useState(0);
  const [following,setFollowing]=useState(0);
  async function getListProduct() {
    try {
      const data = await getMyProduct(1,Number.MAX_SAFE_INTEGER);
      setList(data);
    } catch (err) {
      console.error(err);
    }
  }
  async function getFollower() {
    try{
      const data= await getFollowersCount(user.id);
      setFollower(data.follower_count);
    }
    catch (err){
      console.error(err);
    }
  }
  async function getFollowing() {
    try{
      const data= await getFollowingCount();
      setFollowing(data.following_count);
    }
    catch (err){
      console.error(err);
    }
  }
  useEffect(()=>{
    getListProduct();
    getFollower();
    getFollowing();
  },[])
  return (
    <div className="about-me">
      <span style={{ fontWeight: "500" }}>Thng tin ca hng</span>
      <div className="d-flex flex-column gap-2">
        <span style={{ fontWeight: "500", fontSize: "15px" }}>
          Cam kt vi h thng
        </span>
        <span style={{ opacity: "0.7", fontSize: "15px" }}>
          Sn phm c ng bn phi m bo cht lng, ng vi m t, c
          ngun gc hp php v tun th cc quy nh ca php lut hin hnh.
        </span>
      </div>
      <hr style={{ margin: "0px", opacity: "0.2" }} />
      <div className="row" style={{ fontSize: "15px" }}>
        <div className="d-flex flex-column col-6 gap-2">
          <span style={{ fontWeight: "500" }}>Thng k</span>
          <div
            className="d-flex justify-content-between"
            style={{ fontSize: "14px" }}
          >
            <span>Tng sn phm:</span>
            <span>{listProduct.length}</span>
          </div>
          <div
            className="d-flex justify-content-between"
            style={{ fontSize: "14px" }}
          >
            <span>Ngi theo di:</span>
            <span>{follower}</span>
          </div>
          <div
            className="d-flex justify-content-between"
            style={{ fontSize: "14px" }}
          >
            <span style={{ paddingTop: "5px" }}>ang theo di </span>
            <span>
              {following}
            </span>
          </div>
        </div>
        <div className="d-flex flex-column col-6 gap-2">
          <span style={{ fontWeight: "500" }}>Chnh sch</span>
          <ul style={{ opacity: "0.7", fontSize: "14px", padding: "0px 20px" }}>
            <li style={{ paddingTop: "4px", paddingBottom: "4px" }}>
              Bo hnh 12 thng
            </li>
            <li style={{ paddingTop: "4px", paddingBottom: "4px" }}>
              i tr trong 7 ngy
            </li>
            <li style={{ paddingTop: "4px", paddingBottom: "4px" }}>
              Giao hng ton quc
            </li>
            <li style={{ paddingTop: "4px", paddingBottom: "4px" }}>
              H tr tr gp 0%
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
export default AboutMe;
