import React from "react";
import { User, Lock, Eye, EyeClosed } from "lucide-react";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { checkValidAccount } from "../../utils/utils";
import { login, resendVerificationEmail } from "../../services/authService";
function FormLogin() {
  const [show, setShow] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [user, setUser] = useState({});
  const [message, setMessage] = useState({
    email: "",
    password: "",
    error: "",
  });
  const navigate = useNavigate();
  async function handleLogin(e) {
    e.preventDefault();
    try {
      const data = await login(email, password);
      const userInfo = {
        id: data.id,
        username: data.username,
        email: data.email,
        createdAt: data.created_at,
        avatar: data.avatar_url || "",
      };
      setUser(userInfo);
      sessionStorage.setItem("userId", JSON.stringify(userInfo.id));
      localStorage.setItem("token", data.accessToken);
      navigate("/home");
    } catch (err) {
      if (err.response) {
        if (err.response.status === 400) {
          const listError = err.response.data.errors;
          console.log(listError);
          const msg = {};
          listError.forEach((error) => {
            msg[error.path] = error.msg;
          });
          setMessage(msg);
        } else if (err.response.status === 401) {
          const msg = {
            email: "",
            password: "",
            error: err.response.data.message,
          };
          setMessage(msg);
        }
      }
    }
  }

  return (
    <div className="form-login">
      <span style={{ fontWeight: "500", fontSize: "14px" }}>Email Address</span>
      <form className="input-group position-relative">
        <input
          type="email"
          className="form-control"
          placeholder="Nhp email"
          value={email}
          style={{
            paddingLeft: "2.5rem",
            borderRadius: "0.5rem",
            fontSize: "14px",
          }}
          onChange={(e) => {
            setEmail(e.target.value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") handleLogin(e);
          }}
        />
        <User className="search-icon" />
      </form>
      <span style={{ color: "red" }}>{message.email}</span>
      <span style={{ fontWeight: "500", fontSize: "14px" }}>Mt khu</span>
      <form className="input-group position-relative">
        <input
          type={show ? "text" : "password"}
          className="form-control"
          value={password}
          placeholder="Nhp mt khu"
          style={{
            paddingLeft: "2.5rem",
            borderRadius: "0.5rem",
            fontSize: "14px",
          }}
          onChange={(e) => {
            setPassword(e.target.value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") handleLogin(e);
          }}
        />
        <Lock className="search-icon" />
        <div
          onClick={() => {
            setShow(!show);
          }}
        >
          {show ? (
            <Eye className="eye-icon" />
          ) : (
            <EyeClosed className="eye-icon" />
          )}
        </div>
      </form>
      <span style={{ color: "red" }}>
        {message.password === "" ? message.error : message.password}
      </span>
      <span
        className="underline-text"
        onClick={() => {
          navigate("/reset-password");
        }}
      >
        Qun mt khu?
      </span>
      <div
        className="btn-create"
        style={{ width: "100%" }}
        onClick={handleLogin}
      >
        <span style={{ fontWeight: "500" }}>ng nhp</span>
      </div>
      <span
        style={{
          alignSelf: "center",
          paddingTop: "15px",
          opacity: "0.7",
          fontSize: "15px",
        }}
      >
        Cha c ti khon?
        <span
          className="underline-text"
          onClick={() => {
            navigate("/register");
          }}
        >
        </span>
      </span>
      <span
        style={{
          alignSelf: "center",
          paddingTop: "10px",
          opacity: "0.7",
          fontSize: "14px",
          cursor: "pointer",
          color: "#ff6a00"
        }}
        onClick={async () => {
          if (!email) {
            setMessage({ ...message, error: "Vui lng nhp email  gi li xc thc." });
            return;
          }
          try {
            const res = await resendVerificationEmail(email);
            setMessage({ ...message, error: "", email: "", password: "", success: res.message });
            alert(res.message);
          } catch (err) {
            setMessage({ ...message, error: "C li xy ra." });
          }
        }}
      >
        Cha nhn c email xc thc? Gi li
      </span>
    </div >
  );
}
export default FormLogin;
